.App {
  width: 100%;
  height: 100%;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  overflow: hidden;
  background: #f2a969;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
  height: 100vh;
}

.App.ON {
  background: #f37070;
}

.App.ON .Header {
  -webkit-box-shadow: 1px 0px 25px #ca5b5b;
          box-shadow: 1px 0px 25px #ca5b5b;
}

.Header {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  width: 100wh;
  height: 50px;
  -webkit-box-shadow: 1px 0px 25px #ce8f58;
          box-shadow: 1px 0px 25px #ce8f58;
  top: 0px;
  z-index: 3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px;
}

#timer_icon {
  height: 60px;
  width: 60px;
}

#header_icon {
  width: 30px;
  height: 30px;
}
.icon {
  margin: 10px;
}

@media screen and (max-width: 800px) {
  .Header {
    width: 100%;
    height: 50px;
    padding: 10px 1px;
  }

  #timer_icon {
    height: 40px;
    width: 40px;
  }
  .icon {
    margin: 15px 5px;
  }
}



